import React from 'react'
import {Frame} from 'framer'



function SplashScreen(props){

    const w = window.innerWidth
    const h = window.innerHeight
    const middle = h/2-100

    const d_1 = 2.5
    const d_2 = 3

    return(
        <>
       <Frame   // This is the background animation
            animate={{
               
                y:[0,-h]

            }}
            backgroundColor={props.colour}
            
            width={w}
            height={h}
            size={100}
            transition={{
                 duration:d_1,
                 ease: "easeOut",
                 delay: 2.5
             }}
        
        >                   
                                    {/* Sub title text */}
                <Frame
                        style={{fontFamily:props.font}}
                        color={"white"}
                        className={"font-bold text-4xl w-full object-center"}
                        width={w}
                        y={middle + 200}
                        opacity={0}
                        backgroundColor={props.colour}
                            animate={{

                                opacity:[0,1,1,1,0]

                            }}

                            transition={{
                                duration:d_1,
                                ease: "easeOut",
                                delay: 0.75
                            }}
                >   
                    PlaceHolder #SUBTITLE</Frame>


        </Frame>

                    {/* Title text */}
        <Frame style={{fontFamily:props.font}} className={"font-bold text-9xl w-full object-center"}
        
            backgroundColor={props.colour}
            color={"white"}
            width={w}
            y={middle}
            
            animate={{
                opacity:[1,0.5,0.25],
                y:[middle, -h]
            }}

        

            transition={{
                duration:d_2,
                ease: "easeOut",
                delay: 2.5
            }}
             
            >Unique Walls</Frame>
        </>

    )


}export default SplashScreen